
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import HeaderBack from '../Common/HeaderBack'
import CommentHeader from './CommentHeader'
import CommentItem from './CommentItem'
export default {
name: "comments",
components: {
myHeader: HeaderBack,
myComHeader: CommentHeader,
myComItem: CommentItem,
Scroller,
LoadMore,
},
data() {
return {
number: 23,
header: {
HBL_icon:"icon_back",
HBC_text:"所有评论",
HBR_icon: " ",
},
goodsId: '',
type: '',
showCount: 10,
currentPage: 0,

pullup:{
config: {
content: '上拉加载更多',
pullUpHeight: 40,
height: 40,
autoRefresh: false,
downContent: '释放加载',
upContent: '上拉加载更多',
loadingContent: '',
clsPrefix: 'xs-plugin-pullup-'
},
isAll: false,
isLoadMore: true,
onFetching: false,
},
proCommentRows: [{
A: 10,
AA: 10,
AAA: 10,
total: 30,
}],
proCommentList: [
{
PRO_NAME: "儿童智力检测",
NICKNAME: "支付宝",
COMMENTS: "太好用了,赞一个!!!",
REPLY: "谢谢您的评价,我们会努力做的更好!!!",
CREATE_TIME: "2017-12-08",
HEADIMGURL: ""
},
],
}
},
methods: {
getComments(type) {
if (type === 'all') type = '';

this.type = type;

this.ajaxHandle(true, () => {

if(this.pullup.isAll) {
this.$refs.scroller.disablePullup();
}
});
},

pullupLoading () {
let self = this;
if (!self.pullup.onFetching) {
self.pullup.onFetching = true;
self.pullup.isLoadMore = false;
this.ajaxHandle(false,() => {
setTimeout(() => {
self.$nextTick(() => {
self.$refs.scroller.reset()
self.$refs.scroller.donePullup();
if (self.pullup.isAll) {
self.$refs.scroller.disablePullup();
setTimeout(() => {
self.$refs.isAll.style.display = 'block';
}, 100);
}
});
self.pullup.onFetching = false;
self.pullup.isLoadMore = true;
},0);
});
}
},
ajaxHandle (isClear,callback) {

if(isClear) {
this.proCommentList=[];
this.currentPage = 0;

comTools.showLoading(this);
};

let params = {
type: this.type,
pro_id: this.goodsId,
showCount: this.showCount,
currentPage: this.currentPage,
};
let allComments = this.$store.state.allComments, self = this;

setTimeout((e) => {
if(isClear) {
comTools.hideLoading(self);
self.proCommentRows[0] = self.proCommentRows[0];
}

if (self.currentPage === 3)
self.pullup.isAll = true;

let addData = [], num = self.currentPage*10;
allComments.forEach((ele,i) => {
if (num -1 < i && i < num + 10)
addData.push(ele);
});
self.proCommentList.push.apply(self.proCommentList,addData);

self.currentPage += 1;

callback && callback();
},500)
}

},
created () {
let type = this.$route.params.type;
this.goodsId = this.$route.params.goodsID;
this.getComments(type);
},
mounted () {
let self = this;

setTimeout(function () {
comTools.setHeight(1.75);
self.$refs.scroller.reset();
},0)
}
};



