export default {
  name: "commentHeader",
  props:['value'],
  components: {
  },
  data() {
    return {
      A: this.value.A || 0,
      AA: this.value.AA || 0,
      AAA: this.value.AAA || 0,
      total: this.value.total || 0,
      haveImg: 0,
      additional: 0,        
    }
  },
  methods: {
    getCommentsFn (type) {
      this.$emit('changeType', type)
    },
  }
};



